import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';

const useStyles = makeStyles(theme => ({
  root: {
    '&.footer-fx': {
      '& .main-footer': {
        [theme.breakpoints.up('lg')]: {
          position: 'fixed',
          left: 0,
          bottom: 0,
          width: '100%',
          zIndex: 1,
        },
      },
    },
  },
  rootInner: {
    backgroundColor: theme.mixins.Black(900),
    color: theme.mixins.White(),
  },
  section: {
    display: 'flex',
    flexDirection: 'column',
  },
  upperSection: {
    marginTop: theme.spacing(7),
    marginBottom: 0,
    [theme.breakpoints.up('lg')]: {
      marginTop: theme.spacing(10),
      marginBottom: theme.spacing(5),
    },
  },
  quickLinks: {
    display: 'flex',
    flexDirection: 'column',
    [theme.breakpoints.down('md')]: {
      marginBottom: theme.spacing(4.5),
      '& .quick-link-desktop-view': {
        display: 'none',
      },
    },
    '& ul': {
      paddingTop: 0,
      paddingBottom: 0,
    },
  },

  heading: {
    marginTop: '0',
    paddingBottom: theme.spacing(3),
  },
  newsletter: {
    display: 'flex',
    flexDirection: 'column',
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(5),
    [theme.breakpoints.up('lg')]: {
      marginTop: 0,
      ...theme.mixins.paddingRight(80),
      marginBottom: 0,
    },
    '& h2:not(.follow-us)': {
      paddingBottom: theme.spacing(1),
    },
    '& .sectionSlideDown': {
      transform: 'translateY(0)',
    },
  },
  pr0: {
    paddingRight: '0',
  },
  subscribeDescription: {
    paddingBottom: theme.spacing(4),
    [theme.breakpoints.up('lg')]: {
      paddingBottom: theme.spacing(3),
    },
  },
  emailTextBox: {
    backgroundColor: theme.mixins.Grey(500),
    fontSize: pxToRem(14),
    borderRadius: pxToRem(3),
    '& input': {
      color: theme.mixins.White(),
      padding: theme.spacing(2),
      paddingTop: theme.spacing(1),
      fontFamily: theme.typography.fontFamily.primaryLight,
    },
    '& input::placeholder': {
      color: theme.mixins.White(),
      fontSize: theme.spacing(2),
      marginTop: 0,
    },
    '& svg': {
      color: theme.mixins.White(),
      ...theme.mixins.marginRight(theme.spacing(1)),
    },
    [theme.breakpoints.up('lg')]: {
      maxWidth: theme.spacing(45),
      width: '100%',
    },
  },
  inputLabel: {
    display: 'none',
  },
  downloadApp: {
    display: 'flex',
    flexDirection: 'column',
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(5),
    [theme.breakpoints.up('lg')]: {
      flexDirection: 'row',
      justifyContent: 'space-between',
      marginBottom: theme.spacing(6),
      marginTop: 0,
    },
    '& img.noRendition': {
      width: 'auto',
      height: 'auto',
    },
  },
  downloadAppHeading: {
    marginTop: '0',
    paddingBottom: theme.spacing(3),
  },
  downloadAppLogo: {
    height: pxToRem(42),
    '&:not(:last-child)': {
      ...theme.mixins.paddingRight(theme.spacing(4.5)),
    },
  },
  scrollTop: {
    border: '3px solid white',
    borderRadius: '50%',
    position: 'absolute',
    ...theme.mixins.right(3),
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.right(pxToRem(12)),
    },
  },
  scrollTopHover: {
    '&:hover': {
      backgroundColor: theme.mixins.White(),
      color: theme.mixins.Black(),
    },
    '&:active': {
      backgroundColor: theme.mixins.White(),
      color: theme.mixins.Black(),
    },
  },
  listItem: {
    '&.MuiListItem-root': {
      paddingBottom: theme.spacing(2),
      paddingLeft: 0,
      paddingTop: 0,
      fontFamily: theme.typography.fontFamily.primaryBold,
      textAlign: theme.mixins.textLeft(),

      '&:last-child': {
        paddingBottom: 0,
      },
    },
  },
  linkItem: {
    textDecoration: 'none',
    color: theme.palette.secondary.main,
    '& span': {
      borderBottom: '1px solid transparent',
      '&:hover': {
        borderBottomColor: `${theme.mixins.White()}`,
      },
    },
  },
  followUsLinkItem: {
    textDecoration: 'none',
    color: 'inherit',
    ...theme.mixins.marginRight(theme.spacing(2)),
    marginBottom: theme.spacing(2),
    height: pxToRem(40),
    width: pxToRem(40),
    [theme.breakpoints.up('lg')]: {
      ...theme.mixins.marginRight(theme.spacing(3)),
      marginBottom: theme.spacing(3),
    },
    '& img': {
      backgroundColor: '#474747', // Hardcoded color since no such color is available in mixin
      borderRadius: '50%',
    },
    '&:hover': {
      '& img': {
        backgroundColor: '#8b8c8b', // Hardcoded color since no such color is available in mixin
      },
    },
  },
  socialMediaIcons: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  secondaryLinks: {
    display: 'flex',
    padding: '0',
    [theme.breakpoints.up('lg')]: {
      justifyContent: 'flex-end',
    },
    '& .secondaryLink': {
      color: theme.mixins.White(),
      borderBottom: '1px solid transparent',
      '&:hover': {
        borderBottomColor: `${theme.mixins.White()}`,
      },
    },

    '& .MuiListItem-root': {
      paddingTop: '0',
      paddingBottom: theme.spacing(2),

      [theme.breakpoints.up('lg')]: {
        width: 'auto',
        ...theme.mixins.marginLeft(theme.spacing(6)),
      },
    },
  },
  copyright: {
    display: 'flex',
    justifyContent: 'space-between',
    flexDirection: 'column',
    marginTop: theme.spacing(5),
    marginBottom: theme.spacing(6),
    [theme.breakpoints.up('lg')]: {
      flexDirection: 'row',
      alignItems: 'center',
    },
    '&.detailPage': {
      // we need this static value because our sticky
      // bar is transform to mobile view from 769
      [theme.breakpoints.down('769')]: {
        marginBottom: theme.spacing(15),
      },
    },
    '& .copy-txt': {
      [theme.breakpoints.up('lg')]: {
        textAlign: theme.mixins.textRight(),
      },
    },
    '& img': {
      maxWidth: pxToRem(142),
      width: '100%',
      height: 'auto',
      [theme.breakpoints.down('md')]: {
        marginBottom: theme.spacing(5),
      },
      [theme.breakpoints.up('lg')]: {
        maxWidth: pxToRem(163),
      },
    },
  },
  ScrollToTopContainer: {
    marginTop: theme.spacing(-7),
  },
  divider: {
    backgroundColor: 'grey',
  },
  footerQuickLinks: {
    display: 'flex',
    width: '100%',
    [theme.breakpoints.up('lg')]: {
      display: 'none',
    },
  },
  icpLicense: {
    color: theme.mixins.White(),
    borderBottom: '1px solid transparent',
    '&:hover': {
      borderBottom: '1px solid white',
    },
  },
}));
export default useStyles;
